import React from 'react';
import logo from './logo.svg';
import './App.css';
import Button from '@material-ui/core/Button';
import { loadCSS } from 'fg-loadcss';
import Link from '@material-ui/core/Link';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';


const useStyles = makeStyles(theme => ({
  root: {
      flexGrow: 1,
    },
    menuButton: {
      marginRight: theme.spacing(2),
      color: 'white',
    },
    title: {
      flexGrow: 1,
    },
}));


function App() {


  const classes = useStyles();

  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = event => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };


  React.useEffect(() => {
    loadCSS(
      'https://use.fontawesome.com/releases/v5.1.0/css/all.css',
      document.querySelector('#font-awesome-css'),
    );
  }, []);


  return (
    <div className="App">
      <header className="App-header">
      <AppBar position="fixed">
       <Toolbar variant="dense">
              <IconButton edge="start" className={classes.menuButton} color="white" aria-label="menu">
                  <MenuIcon aria-controls="fade-menu" aria-haspopup="true" onClick={handleClick} />
              </IconButton>
              <Menu
                 id="simple-menu"
                 anchorEl={anchorEl}
                 keepMounted
                 open={Boolean(anchorEl)}
                 onClose={handleClose}
               >
               <MenuItem>
                 <Link href="https://github.com/alex-bormotov/AXE-Bot#Install" color="inherit">
                   Install
                  </Link>
                </MenuItem>
                <MenuItem>
                  <Link href="https://github.com/alex-bormotov/AXE-Bot#Config" color="inherit">
                    Config
                   </Link>
                 </MenuItem>
                <MenuItem>
                  <Link href="https://github.com/alex-bormotov/AXE-Bot#Update" color="inherit">
                    Update
                   </Link>
                 </MenuItem>
                 <MenuItem>
                   <Link href="https://github.com/alex-bormotov/AXE-Bot#Donate" color="inherit">
                     Donate
                    </Link>
                  </MenuItem>
               </Menu>
              <Typography variant="h6" color="inherit">
                 Axe Bot - Automated trading bot
              </Typography>
       </Toolbar>
      </AppBar>
          <img src={logo} className="App-logo" alt="logo" />
          <br/>
          <Typography variant="h6" color="inherit">
             It's OpenSource bot, you may use it for FREE!
          </Typography>
          <br/>
          <Button variant="contained" color="primary">
            <Link href="https://github.com/alex-bormotov/AXE-Bot" color="inherit">
              Get Started
            </Link>
          </Button>
      </header>
    </div>
  );
}

export default App;
